import React, { useEffect, useState, useRef } from "react";

import { getStore, updStore, gen16CodeId, qryStore, fetchEncryptStream } from "../../../stores/Utils";

import {  TextInput_Cmpt } from "../../../components";


import "./dashboard.css"
import { CloseModal } from "../../../components/DialogHRM";

const TextInput = TextInput_Cmpt();


const frmNm = "new_business"

const AddBusiness  = ({ data, confirm }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};


	const save  = async ()  => {
		let ffm = getStore(frmNm);

		ffm["businessId"] = gen16CodeId();

		let bdyq2 = {...getStore(qryStore())?.add_busnss};
		bdyq2.form=ffm;
		const res = fetchEncryptStream(bdyq2);
		const td = await res;
		if (td) {
			updStore(frmNm, {});
			confirm && confirm();
			data?.modalID && CloseModal({id:data?.modalID})
		}
		///let pdfUri = await generateTranslateAddClient(ffm, true, true);
		///window.open(pdfUri);
	};


	


	return (
		<div style={{
			maxHeight: "80vh",
			width: "560px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px 30px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Agregar Business
				</p>
				<div className="flexSpace"/>
				
			</div>

			
			<TextInput  
				init={getStore(frmNm)?.["name"]} 
				label={"Nombre"} 
				width={520} 
				updChanges={(e)=>updFrm("name",e)}
			/>

			
			<TextInput  
				init={getStore(frmNm)?.["description"]} 
				label={"Descripcion"} 
				width={520} 
				updChanges={(e)=>updFrm("description",e)}
			/>


		

			
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				{ getStore(frmNm)?.["name"]  ?
				<div className="card2" style={{padding:"20px 10px 0"}}>
					<button onClick={save}>
						Guardar
					</button>
				</div>
				: null}

			</div>
		

			
		</div>
	);
};

export default AddBusiness;

